html, body {
  width: 100%;
  height: 100%;
  box-sizing: border-box;
  scroll-behavior: smooth;
  background: #fff;
  margin: 0;
  padding: 0;
  scroll-padding: 10rem;
  font-family: Poppins;
}

section {
  margin-top: 40px;
}

@keyframes scrollIndicator {
  0% {
    opacity: 1;
    bottom: 80%;
  }

  100% {
    opacity: 0;
    bottom: 20%;
  }
}

.content {
