  font-size: 1.2rem;
  font-weight: 300;
  text-decoration: none;
}

@media screen and (max-width: 900px) {
  .menu:hover {
    height: 2.6rem;
    background: #f9f9f9;
    border-radius: .8rem;
    flex-flow: column wrap;
    justify-content: flex-start;
    align-items: flex-start;
    margin: 1.6rem;
    padding: 2rem;
    font-size: 1.4rem;
    font-weight: 400;
    transition: all .3s;
    display: flex;
  }

  .menu-expand, .menu[disabled]:hover, .mouse-scroll, .menu-right .account {
    display: none;
  }

  .menu-right {
    flex: 1;
    justify-content: flex-end;
    align-items: center;
    margin-right: 40px;
    display: flex;
    position: fixed;
    right: 0;
  }

  #menu-center {
    z-index: 100;
    background-color: #f9f9f9;
    display: none;
    position: absolute;
  }

  #menu-center.active {
    width: 100%;
    flex-direction: column;
    margin-top: 20px;
    padding: 4rem 0;
    display: flex;
    position: relative;
  }

  #menu-center.active a {
    margin: .2rem 0;
    font-size: 1.2rem;
    line-height: 70px;
  }

  #menu-toggle-btn {
    display: block;
  }

  #btn-mobile {
    background-color: #0000;
    border: none;
    gap: .5rem;
    display: flex;
  }

  #hamburguer {
    width: 20px;
    border-top: 2px solid;
  }

  #hamburguer:after, #hamburguer:before {
    content: "";
    width: 20px;
    height: 2px;
    background: currentColor;
    margin-top: 5px;
    transition: all .3s;
    display: block;
    position: relative;
  }

  #menu-toggle-btn.active #hamburguer {
    border-top-color: #0000;
  }

  #menu-toggle-btn.active #hamburguer:before {
    transform: rotate(135deg);
  }

  #menu-toggle-btn.active #hamburguer:after {
    top: -7px;
    transform: rotate(-135deg);
  }
}

